.gaming_library-card {
  flex: 1 1 100%;
}

.gaming_library-card ul {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid var(--color-primary);
  padding: 25px 0;
}

.gaming_library-card h4 {
  font-size: 1rem;
}

.gaming_library-card li span {
  color: #aaa;
}