.hero_main {
  background: url('../../assets/imgs/banner-bg.jpg') no-repeat top right/cover;
  background-color: #27292a;
  color: var(--color-light);
  border-radius: 25px;
  padding: 50px;
}

.hero_text {
  max-width: 500px;
}

.hero_title {
  font-size: 60px;
  margin-bottom: 25px;
  font-weight: 900;
}

.hero_title em {
  font-style: normal;
  color: var(--color-primary);
}

.hero_subtitle {
  font-size: 20px;
  color: var(--color-light);
  font-weight: 400;
  margin-bottom: 25px;
}