
.most_popular-item {
  flex: 1 25%;
}

.card_wrapper {
  background-color: var(--color-dark);
  border-radius: 12.5px;
  padding: 25px;
  margin: 10px;
}

.most_popular-img {
  width: 100%;
  border-radius: 12.5px;
  margin-bottom: 25px;
}

.most_popular-content {
  display: flex;
  justify-content: space-between;
}

.most_popular-title {
  font-size: 14px;
  line-height: 1.5rem;
}

.most_popular-content ul li i {
  margin-right: 8px;
}

.most_popular-title span {
  color: #aaa;
}